.form {
    line-height: 25px;
    min-width: 150px;
    max-width: 450px;
    margin: auto;
    padding: 30px;
    border-radius: 4px;
    background-color: rgba(228, 233, 235, 0.726);
    box-shadow: 5px 5px 5px cadetblue;
}

.form__fieldset {
    border-radius: 4px;
}

.form__legend {
    background-color: rgb(58, 166, 209);
    border-radius: 4px;
    color: white;
    padding: 4px;
}

.form__text {
    display: inline-block;
        width: 100%;
}

.form__field {
    width: 25%;
    min-width: 30px;
    max-width: 200 px;
    margin: auto;
    border-radius: 4px;
    text-align: right;
}

.form__exchangeButton {
    border: 0px;
    background-color: rgb(30, 157, 207);
    width: 100%;
    padding: 5px;
    border-radius: 4px;
    color: white;
    font-family: 'Arimo', sans-serif;
    font-size: medium;
    display: block;
    margin-top: 10px;
}

.form__exchangeButton:hover {
    background-color: rgb(76, 184, 226);
}

.form__resetButton {
    width: 100%;
    margin-top: 10px;
    border: 0px;
    border-radius: 4px;
    background-color: rgba(199, 117, 51, 0.726);
    padding: 5px;
    color: white;
    font-family: 'Arimo', sans-serif;
    font-size: medium;
    display: block;
}

.form__resetButton:hover {
    background-color: rgba(202, 147, 101, 0.733);
}